  height: 1%;
  flex: 1;
}
app-user-menu .calendar-modal .p-dialog-content .fc button {
  background: #D32E16;
  border: none;
  box-shadow: none;
}
app-user-menu .calendar-modal .p-dialog-content .fc button:active {
